main {
    padding: 0;
}

.home__page {
    display: flex;
}

.center__div {
    width: 100%;
}

.center__div header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    border: 1px solid #D8D8D8;
    border-top: none;
}

.text__add-block {
    display: flex;
    align-items: end;
    justify-content: space-between;
    border: 1px solid #D8D8D8;
    border-top: none;
    padding: 15px;
}

.text-add__div {
    display: flex;
}

.text-icon__list {
    display: flex;
    align-items: center;
    gap: 20px;
}

.title-text input {
    padding: 5px;
    margin-bottom: 51px;
    border: none;
}

.title-text {
    margin-left: 15px;
}

.btn-text {
    font-size: 17px;
    line-height: 17px;
    background-color: #1DA1F2;
    outline: none;
    border: none;
    border-radius: 120px;
    padding: 15px 30px 18px 30px;
    cursor: pointer;
    color: #fff;
}

.text_add-center ul {
    display: flex;
    align-items: center;
    gap: 100px;
}

.text_add-center ul li {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.successfull__item {
    padding: 35px 25px 10px 25px;
    border: 1px solid #D8D8D8;
    border-top: none;
}

.img_add-center {
    display: flex;
    gap: 15px;
}

.text_add-center h4 {
    font-size: 20px;
    padding-bottom: 5px;
}

.text_add-center p {
    font-size: 16px;
    padding-bottom: 22px;
}

.text_add-center {
    padding-bottom: 22px;
}

.text_add-center h4 span {
    font-size: 14px;
    opacity: 0.5;
}

.img-kebab {
    padding-bottom: 22px;
}

.right__div {
    width: 100%;
    padding: 20px 0 0 20px;
}

.right__form {
    margin-bottom: 20px;
}

.right__form input {
    width: 100%;
    padding: 15px 0 15px 44px;
    border-radius: 31px;
    background: #EFF3F4;
    border: none;
}

.list__right {
    border-radius: 10px;
    background: #F7F9F9;
    padding: 15px 20px;
    margin-bottom: 20px;
}

.list__right li {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
}

.list__right ul li:nth-child(1) h4 {
    color: #000;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.right-item__title p {
    color: #393B41;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    opacity: 0.6000000238418579;
}

.right-item__title h5 {
    color: #000;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.link_right {
    color: #1DA1F2;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;

}

.link_right:hover {
    border-bottom: 1px solid #1DA1F2;
}

.sticky {
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #fff;
    padding-top: 10px;
}

.follow__block {
    padding: 20px 15px 25px 15px;
    border-radius: 10px;
    background: #F7F9F9;
}

.follow__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
}

.follow__block h3 {
    margin-bottom: 25px;
}

.follow__item div h6 {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.follow__item div a {
    color: #000;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    opacity: 0.6000000238418579;
}

.follow__btn {
    border: none;
    cursor: pointer;
    padding: 8px 10px;
    border-radius: 50px;
    background: #000;
    color: #FFF;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
}


.show__more {
    color: #1DA1F2;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
}

.follow__block {
    margin-bottom: 30px;
}

.follow__subtitles {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
}

.follow__subtitles span a {
    flex: 0 0 33.33%;
    color: #536471;
    font-size: 16px;
    padding: 10px;
    text-align: center;
}